

.loading__body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background: #121213;
}

.loading__load {
    position: relative;
    -webkit-box-reflect: below -20px linear-gradient(transparent, #0002);
}

.loading__load::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 2px;
    height: 100%;
    background: #03e9f4;
    animation: blinkCursor 0.8s steps(3) infinite;
}

@keyframes blinkCursor
{
    0%, 75%
    {
        opacity: 1;
    }
    76%,100%
    {
        opacity: 0;
    }
}

.loading__load h1 {
    font-family: consolas;
    position: relative;
    color: #03e9f4;
    letter-spacing: 5px;
    font-size: 4em;
    text-transform: uppercase;
    animation: typing 4s steps(10) infinite;
    overflow: hidden;
}

@keyframes typing 
{
    0%,90%,100%
    {
        width: 0;
    }
    30%,60%
    {
        width: 401.88px;
    }
}